import { Paging, PagingForm } from '@/models';
import { FormCache } from '@/utils';
import {
  observable,
  action,
  computed,
  autorun,
  toJS,
  makeObservable,
  // makeObservable,
} from 'mobx';

// TODO: ÊµèËßàÂô®Á™óÂè£ÔºåÈÉ®ÂàÜtoJSËé∑ÂèñÊï∞ÊçÆÔºå‰ª•ÂêéÂà†Èô§
window.toJS = toJS;
/**
 * StoreBase
 * Â¶ÇÊûúË¶ÅÂºÄÂêØÁºìÂ≠òÔºåÂú®constructorÂºÄÂêØ
 *  this.getCache();
 *  this.setCache();
 * ÊàñËÄÖÔºåËá™Â∑±ÊâãÂä®Ë∞ÉÁî®ÈÖçÁΩÆ
 */
class StoreBase<T = any> {
  name = 'StoreBase';
  // KEYÁöÑ‰ΩúÁî®ÊòØÂØπÈÇ£‰∫õÂîØ‰∏ÄÂåñË∑ØÁî±Â§ÑÁêÜÔºåÊØîÂ¶ÇË∑ØÁî±È°µÈù¢/:id‰∏çÂêåÁöÑ
  // Â¶ÇÊûúÊ≤°Êúâ‰∏çÂêåÔºå‰∏ÄËà¨ÈªòËÆ§ÂÄºÂç≥ÂèØ
  key = 'KEY';
  Storage = localStorage;
  time = 60 * 60;
  // ÁºìÂ≠òÂΩìÂâçÈ°µÈù¢Êï∞ÊçÆ
  @observable data: SimpleObject | T = {};

  constructor() {
    // Á¨¨‰∏ÄÊ¨°Ëé∑ÂèñÔºåÂêåÊó∂Êõ¥Êñ∞Ëé∑ÂèñÊó∂Èó¥
    if (this.name !== 'StoreBase') {
      // the autocache must handle in children
      this.getCache();
      this.setCache();
    }
    makeObservable(this, {
      data: observable,
      setData: action.bound,
    });
    // console.log('storeBase constructor', this.SaveName);
  }
  /**
   *
   * @param data
   * @param options: isSet=trueÊòØÁõ¥Êé•Ë¶ÜÁõñ
   */
  @action.bound
  setData(data: any, { isSet = false }: any = {}) {
    if (isSet) {
      this.data = data;
    } else {
      if (this.data) {
        this.data = { ...this.data, ...data };
      } else {
        this.data = data;
      }
    }
  }
  @computed get dataToString() {
    return JSON.stringify(this.data);
  }
  @observable dispose: () => any = () => false;
  // Ëá™Âä®ÁºìÂ≠ò
  autoCache = () => {
    // console.groupCollapsed(this.SaveName + ' cache ' + this.name);
    // console.log('cacheData', this.SaveName, this.data);
    // console.log('data', toJS(this.data));
    // console.groupEnd();
    const data = toJS(this.data);
    // Object.keys(data as object);
    data && FormCache.set(data, this.SaveName, this.Storage, this.time);
  };
  // Âà∑Êñ∞ÂêéËé∑Âèñcache Â¶ÇÊûúÈúÄÊúâÂÖ∂‰ªñ Ë¶ÜÁõñ
  getCache = () => {
    this.data = FormCache.get(this.SaveName, this.Storage) ?? {};
    // console.log('this.getCache', this.data, toJS(this.data));
    return this.data;
  };
  // ËÆæÂÆöÁºìÂ≠ò
  setCache = () => {
    // console.log('this.dispose', this.dispose);
    // this.dispose && this.dispose();
    this.dispose = autorun(() => {
      this.autoCache();
    });
  };
  // ÂèñÊ∂àÁºìÂ≠ò
  cancelCache = () => {
    // Â¶ÇÊûúÂÖ∑ÊúâËá™Âä®ÁºìÂ≠òÔºåÂèñÊ∂àÁºìÂ≠ò
    this.dispose && this.dispose();
    this.dispose = () => false;
  };
  // Ê∏ÖÈô§ÁºìÂ≠ò
  clearCache = () => {
    this.data = {};
    FormCache.clear(this.SaveName, this.Storage);
  };
  @computed get SaveName() {
    return this.name + this.key;
  }
}
/**
 * StorePage
 * ÂäüËÉΩ
 * 1. ‰∏æ‰æã:ÊúâË°®Ê†ºÁöÑÈ°µÈù¢ÂÖÉÁ¥†
 * 2. ‰ΩøÁî®‰Ωú‰∏∫ÔºåÈªòËÆ§ËÆæÂÆöÁöÑstoreÔºà‰∏ªÈ°µÔºåÂÖ∑ÊúâË°®ÂçïÁöÑÔºâ
 * ‰∏çÂÖ∑Â§áÂ§ö‰∏™ÈôÑÂä†Á±ªÂûãÔºåÂ≠©Â≠êÈ°µÈù¢Ôºå‰ΩøÁî®ÁöÑË°®ÂçïÁ±ªÂûãÔºå‰ΩÜÊòØÁ±ªÂûãÁ±ª‰ºº
 * ----
 * ÊÄùËÄÉÂ§ö‰∏™Êï∞ÊçÆÁ±ªÂûãÔºåÁªÑ‰ª∂Á±ªÂûãÂ∫îÁî®ÔºåÂª∫ËÆÆStorePage
 * Ê≥®ÊÑèÔºö Ê†πÊçÆ‰∏çÂêåÁöÑÈ°µÈù¢ÔºåÂ¶ÇÊûúÊúâÂ§ö‰∏™ÁöÑËØùÔºåÂª∫ËÆÆÔºåÂ§öÁ±ªÂûãÂ§ÑÁêÜ
 * ------
 */
class StorePage<T = any, S = SimpleObject, V = any> extends StoreBase<V> {
  name = 'StorePage';
  key = 'KEY';
  /**
   * ÂàÜÈ°µÊï∞ÊçÆ
   */
  @observable
  pageData: Paging<T> = {
    list: [],
  };
  /**
   * Ë°®ÂçïÊï∞ÊçÆ, ÈªòËÆ§‰∏∫Á©∫Ôºårefresh‰ºöÊõ¥Êñ∞Ôºà‰∏çÁ©∫Ôºâ
   */
  @observable
  formData: PagingForm<S> | PagingForm<SimpleObject> | undefined = undefined;
  /**
   * Êõ¥Êñ∞Ë°®Âçï
   */
  @action.bound
  refresh() {
    if (this.formData) {
      this.formData = { ...this.formData };
    } else {
      this.formData = {};
    }
  }
  @action.bound
  setPageData(payload: any) {
    this.pageData = payload;
  }
  @action.bound
  defaultPageData() {
    if (this.pageData.list.length > 0) this.pageData = { list: [] };
  }
  @action.bound
  setFormData(payload: any, { isSet = false }: any = {}) {
    if (isSet) {
      this.formData = payload;
      return;
    }
    const formData = this.formData;
    this.formData = { ...formData, ...payload };
    // console.log('set', this.formData);
  }
  @action.bound
  defaultFormData() {
    this.formData = undefined;
  }
}

export { StoreBase, StorePage };
