type ToCheckParam = {
  reg: RegExp | string | Reg;
  value: string;
  message: string;
  isPromise: boolean;
};

type CheckVaild = {
  reg: RegExp;
  value: string;
  message: string;
  isPromise: boolean;
};
type Reg = {
  pattern: RegExp;
  message: string;
};
const checkVaild = ({ reg, value, message, isPromise }: CheckVaild) => {
  if (isPromise) {
    if (!reg.test(value)) {
      return Promise.reject(message);
    }
    return Promise.resolve();
  } else {
    return Promise.resolve(reg.test(value));
  }
};

