import React, { ReactNode } from 'react';
import { ModalProps } from '@/models';
import Modal from 'antd/lib/modal/Modal';

type PropsModalWrapper = {
  children: ReactNode;
};
type Props = ModalProps<PropsModalWrapper>;
// useway
/*

type SelfProps = {
  form: any;
  BrandInfo: any;
};
type Props = ModalProps<SelfProps>;


*/
//

// Âõ†‰∏∫ÊòæÁ§∫visibleÊòØÂ§ñÂ±ÇÊéßÂà∂
// Âõ†Ê≠§
// ÂØπmodalÂ§ÑÁêÜÔºåÂØπonOkÂíåonCancelËøõË°åÈªòËÆ§Â§ÑÁêÜ
const ModalWrapper = ({
  title,
  visible,
  setVisible,
  onOk,
  onCancel,
  children,
  ...rest
}: Props) => {
  // onOkÂíåokCancel‰∏çÂ≠òÂú®ÂàôËÆæÁΩÆÈªòËÆ§ÁöÑÈöêËóè
  const onHidden = () => setVisible(false);
  onOk = onOk || onHidden;
  onCancel = onCancel || onHidden;
  return (
    <Modal
      visible={visible}
      title={title}
      onOk={onOk}
      onCancel={onCancel}
      {...rest}
    >
      {children}
    </Modal>
  );
};

export { ModalWrapper };
