/**
 * ÂØπÈ°µÈù¢‰∏äËæìÂÖ•ÁöÑÂØÜÁ†ÅËøõË°åÂä†ÂØÜ‰º†ËæìÁªôÂêéÂè∞ËøõË°åÈ™åËØÅÔºåÂØπËøîÂõûÁöÑÊï∞ÊçÆËøõË°åËß£ÂØÜÔºåÂú®È°µÈù¢Â±ïÁ§∫
 */
import CryptoJS from 'crypto-js';

/*
 * ÂØπÂØÜÁ†ÅËøõË°åÂä†ÂØÜÔºå‰º†ËæìÁªôÂêéÂè∞ËøõË°åÈ™åËØÅ
 * @param  {String}     word    ÈúÄË¶ÅÂä†ÂØÜÁöÑÂØÜÁ†Å
 * @param  {String}     keyStr    ÂØπÂØÜÁ†ÅÂä†ÂØÜÁöÑÁßòÈí•
 * @return {String}     Âä†ÂØÜÁöÑÂØÜÊñá
 * */
export const encrypt = (word: string, keyStr: string) => {
  // Âä†ÂØÜ
  keyStr = keyStr ? keyStr : 'ABGHNJHGSHUYG12';
  const key = CryptoJS.enc.Utf8.parse(keyStr);
  const srcs = CryptoJS.enc.Utf8.parse(word);
  const encrypted = CryptoJS.AES.encrypt(srcs, key, {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7,
  });
  return encrypted.toString();
};
/*
 * ÂØπÂä†ÂØÜ‰πãÂêéÁöÑÂØÜÊñáÂú®È°µÈù¢‰∏äËøõË°åËß£ÂØÜÔºå‰ª•‰æøÁî®Êà∑ËøõË°å‰øÆÊîπ
 * @param  {String}     word    ÈúÄË¶ÅÂä†ÂØÜÁöÑÂØÜÁ†Å
 * @param  {String}     keyStr    ÂØπÂØÜÁ†ÅÂä†ÂØÜÁöÑÁßòÈí•
 * @return {String}      Ëß£ÂØÜÁöÑÊòéÊñá
 * */
export const decrypt = (word: string, keyStr: string) => {
  // Ëß£ÂØÜ
  keyStr = keyStr ? keyStr : 'ABGHNJHGSHUYG12';
  const key = CryptoJS.enc.Utf8.parse(keyStr);
  const decrypt = CryptoJS.AES.decrypt(word, key, {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7,
  });
  return CryptoJS.enc.Utf8.stringify(decrypt).toString();
};

window.crypt = { encrypt, decrypt };
